import React, { useEffect, useState } from "react"
import axios from "axios";
import { withRouter } from "react-router-dom";
import './index.scss';
const Videos: React.FunctionComponent<any> = (props) => {
    const [data, setData] = useState<any>({});
    useEffect(() => {
        const videoId = props.match.params.id;
        const url = 'https://www.youtube.com/watch?v=' + videoId;
        console.log(videoId);
        //https://subservices.cangokceaslan.com/api/videos/k51YBiMvISs
        axios.get("https://subservices.cangokceaslan.com/api/videos/" + videoId).then((response: any) => {
            console.log(response?.data)
            setData(response?.data?.data?.items[0]?.snippet)
        })
    }, [])

    return (
        <div className="container video-view">
            <div className="row">
                <div className="col-lg-1"></div>
                <div className="col-lg-10">
                    <iframe width={"100%"}
                        height="100vh"
                        className="youtube-video-embedder"
                        src={`https://www.youtube.com/embed/${props?.match?.params?.id}?fs=1&iv_load_policy=3&showinfo=0&rel=0&cc_load_policy=0&start=0&end=0&mute=0&loop=1&color=white&controls=0&modestbranding=1&playsinline=1&rel=0&enablejsapi=1`}
                        title="YouTube video player"
                        frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowFullScreen>
                    </iframe>
                    <h1>{data?.title}</h1>
                    <hr />
                    <div>
                        {(data?.description || "").split('\n').map((item: any, index: number) => {
                            if (item.indexOf("https://") > -1) {
                                return <b key={index}>{item}</b>
                            } else {
                                return <p key={index}>{item}</p>
                            }
                        })}
                    </div>
                </div>
                <div className="col-lg-1"></div>
            </div>
        </div>
    )
}

export default withRouter(Videos);